<template>
  <div>
    <el-form label-position="top" label-width="90px">
      <el-form-item label=" " prop="text" class="user-type">
        <el-radio-group v-model="nodeProps.assignedType">
          <el-radio v-for="t in approvalTypes" :label="t.type" :key="t.type">{{ t.name }}</el-radio>
        </el-radio-group>
        <div v-if="nodeProps.assignedType === 'ASSIGN_USER'">
          <el-form-item label="" prop="text" class="approve-end">
            <el-button size="mini" icon="el-icon-plus" type="primary" @click="openForAssigneeUser" round></el-button>
            <org-items v-model="nodeProps.assignedUser"/>
          </el-form-item>
        </div>
        <div v-else-if="nodeProps.assignedType === 'ROLE'">
          <el-form-item label="" prop="text" class="approve-end">
            <el-button size="mini" icon="el-icon-plus" type="primary" @click="openForAssigneeRole" round></el-button>
            <org-items v-model="nodeProps.role"/>
          </el-form-item>
        </div>
        <!--<div v-else-if="nodeProps.assignedType === 'SELF_SELECT'">
          <el-radio-group size="mini" v-model="nodeProps.selfSelect.multiple">
            <el-radio-button :label="false"></el-radio-button>
            <el-radio-button :label="true"></el-radio-button>
          </el-radio-group>
        </div>-->
        <div v-else-if="nodeProps.assignedType === 'LEADER_TOP'">
          <el-form-item label="" prop="text" class="approve-end">
            <el-radio-group v-model="nodeProps.leaderTop.endCondition">
              <el-radio label="TOP"></el-radio>
              <el-radio label="LEAVE"></el-radio>
            </el-radio-group>
            <div class="approve-end-leave" v-if="nodeProps.leaderTop.endCondition === 'LEAVE'">
              <span> </span>
              <el-input-number :min="1" :max="20" :step="1" size="mini" v-model="nodeProps.leaderTop.level"/>
              <span> </span>
            </div>
          </el-form-item>
        </div>
        <div v-else-if="nodeProps.assignedType === 'LEADER'">
          <el-form-item label="" prop="text">
            <span> </span>
            <el-input-number :min="1" :max="20" :step="1" size="mini"
                             v-model="nodeProps.leader.level"></el-input-number>
            <span> </span>
            <div style="color: #409EFF; font-size: small;"> 1</div>
          </el-form-item>
        </div>
        <div v-else-if="nodeProps.assignedType === 'FORM_USER'">
          <el-form-item label="" prop="text" class="approve-end">
            <el-select style="width: 80%;" size="small" v-model="nodeProps.formUser" placeholder="">
              <el-option v-for="op in forms" :label="op.title" :value="op.id"></el-option>
            </el-select>
          </el-form-item>
        </div>
        <div v-else>
          <span class="item-desc"></span>
        </div>
      </el-form-item>

      <el-divider></el-divider>
      <el-form-item label=" " prop="text" class="line-mode">
        <el-radio-group v-model="nodeProps.nobody.handler">
          <el-radio label="TO_PASS"></el-radio>
          <el-radio label="TO_REFUSE"></el-radio>
          <el-radio label="TO_ADMIN"></el-radio>
          <el-radio label="TO_USER"></el-radio>
        </el-radio-group>

        <div style="margin-top: 10px" v-if="nodeProps.nobody.handler === 'TO_USER'">
          <el-button size="mini" icon="el-icon-plus" type="primary" @click="openForNobodyAssignee" round></el-button>
          <org-items v-model="nodeProps.nobody.assignedUser"/>
        </div>

      </el-form-item>

      <div v-if="showMode">
        <el-divider/>
        <el-form-item :label="' '+nodeProps.nobody.tips" prop="text" class="approve-mode">
          <el-radio-group v-model="nodeProps.mode">
            <el-radio label="NEXT"> </el-radio>
            <el-radio label="AND"></el-radio>
            <el-radio label="OR"></el-radio>
          </el-radio-group>
        </el-form-item>
      </div>

      <el-divider></el-divider>
      <!--  -->
      <div v-if="false">
      <el-form-item label=" " prop="text">
        <el-switch inactive-text="" active-text="" v-model="nodeProps.sign"></el-switch>
        <el-tooltip class="item" effect="dark" content="" placement="top-start">
          <i class="el-icon-question" style="margin-left: 10px; font-size: medium; color: #b0b0b1"></i>
        </el-tooltip>
      </el-form-item>
      <el-form-item label="  0 " prop="timeLimit">
        <el-input style="width: 180px;" placeholder="" size="small" type="number"
                  v-model="nodeProps.timeLimit.timeout.value">
          <el-select style="width: 75px;" v-model="nodeProps.timeLimit.timeout.unit" slot="append" placeholder="">
            <el-option label="" value="D"></el-option>
            <el-option label="" value="H"></el-option>
          </el-select>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="level" v-if="nodeProps.timeLimit.timeout.value > 0">
        <el-radio-group v-model="nodeProps.timeLimit.handler.type">
          <el-radio label="PASS"></el-radio>
          <el-radio label="REFUSE"></el-radio>
          <el-radio label="NOTIFY"></el-radio>
        </el-radio-group>
        <div v-if="nodeProps.timeLimit.handler.type === 'NOTIFY'">
          <div style="color:#409EEF; font-size: small"></div>
          <el-switch inactive-text="" active-text="" v-model="nodeProps.timeLimit.handler.notify.once"></el-switch>
          <span style="margin-left: 20px" v-if="!nodeProps.timeLimit.handler.notify.once">
							
							<el-input-number :min="0" :max="10000" :step="1" size="mini"
                               v-model="nodeProps.timeLimit.handler.notify.hour"/>
							
						</span>
        </div>
      </el-form-item>
      </div>
      <!--  -->
      <el-form-item label="  ">
        <el-radio-group v-model="nodeProps.refuse.type">
          <el-radio label="TO_END"></el-radio>
          <el-radio label="TO_BEFORE"></el-radio>
          <el-radio label="TO_NODE"></el-radio>
        </el-radio-group>
        <div v-if="nodeProps.refuse.type === 'TO_NODE'">
          <span>:</span>
          <el-select style="margin-left: 10px; width: 150px;" placeholder="" size="small" v-model="nodeProps.refuse.target">
            <el-option v-for="(node, i) in nodeOptions" :key="i" :label="node.name" :value="node.id"></el-option>
          </el-select>
        </div>
      </el-form-item>
    </el-form>
    <!--  -->
    <org-picker multiple :type="orgPickerType" :selected="orgPickerChecked" ref="orgPicker" @ok="orgPickerOk"/>
  </div>
</template>

<script>
import OrgPicker from "@/components/common/OrgPicker";
import OrgItems from "../OrgItems";

export default {
  name: "ApprovalNodeConfig",
  components: {OrgPicker, OrgItems},
  props: {
    config: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  watch: {
  },
  data() {
    return {
      orgPickerType: 'user',
      orgPickerChecked: [],
      orgPickerMod: null,// user  role  nobodyUser 
      approvalTypes: [
        {name: '', type: 'ASSIGN_USER'},
        {name: '', type: 'ROLE'},
        //{name: '', type: 'SELF_SELECT'},
        {name: '', type: 'SELF'},
        {name: '', type: 'LEADER_TOP'},
        {name: '', type: 'LEADER'},
        {name: '', type: 'FORM_USER'}
      ]
    }
  },
  computed: {
    nodeProps() {
      return this.$store.state.selectedNode.props
    },
    forms() {//
      return this.$store.state.design.formItems.filter(f => {
        return f.name === 'UserPicker'
      })
    },
    nodeOptions() {//
      let values = []
      const excType = ['EMPTY', "CONDITION", "CONDITIONS", "CONCURRENT", "CONCURRENTS"]//'ROOT'
      this.$store.state.nodeMap.forEach((v) => {
        if (excType.indexOf(v.type) === -1) {
          values.push({id: v.id, name: v.name})
        }
      })
      return values
    },
    showMode() {//
      switch (this.nodeProps.assignedType) {
        case "ASSIGN_USER"://
          this.nodeProps.nobody.tips = "";
          return this.nodeProps.assignedUser.length > 0;
        case "SELF_SELECT"://
          this.nodeProps.nobody.tips = "";
          return this.nodeProps.selfSelect.multiple;
        case "LEADER_TOP"://
          this.nodeProps.nobody.tips = "";
          return true;
        case "FORM_USER":
          this.nodeProps.nobody.tips = "";
          return true;
        case "ROLE":
          this.nodeProps.nobody.tips = "";
          return true;
        default:
          return false;
      }
    }
  },
  methods: {
    openForAssigneeUser() {
      this.orgPickerMod = 'user';//
      this.orgPickerType = "user";
      this.orgPickerChecked = this.config.assignedUser || [];
      console.log(this.orgPickerMod, this.orgPickerType, this.orgPickerChecked)
      this.$nextTick(() => {
        this.$refs.orgPicker.show();
      });
    },
    openForAssigneeRole() {
      this.orgPickerMod = 'role';//
      this.orgPickerType = "role";
      this.orgPickerChecked = this.config.role || [];
      this.$nextTick(() => {
        this.$refs.orgPicker.show();
      });
    },
    openForNobodyAssignee() {
      this.orgPickerMod = 'nobodyUser';//
      this.orgPickerType = "user";
      this.orgPickerChecked = this.config.nobody.assignedUser || [];
      this.$nextTick(() => {
        this.$refs.orgPicker.show();
      });
    },
    orgPickerOk(list) {
      if("user"===this.orgPickerMod) {
        this.config.assignedUser.length = 0;
        list.forEach(val => this.config.assignedUser.push(val))
      }
      if("role"===this.orgPickerMod) {
        this.config.role.length = 0;
        list.forEach(val => this.config.role.push(val))
      }
      if("nobodyUser"===this.orgPickerMod) {
        this.config.nobody.assignedUser.length = 0;
        list.forEach(val => this.config.nobody.assignedUser.push(val))
      }
    },
    removeOrgItem(index) {
      this.select.splice(index, 1)
    }
  }
}
</script>

<style lang="less" scoped>
.user-type {
  /deep/ .el-radio {
    width: 110px;
    margin-top: 10px;
    margin-bottom: 20px;
  }
}

/deep/ .line-mode {
  .el-radio {
    width: 150px;
    margin: 5px;
  }
}

/deep/ .el-form-item__label {
  line-height: 25px;
}

/deep/ .approve-mode {
  .el-radio {
    float: left;
    width: 100%;
    display: block;
    margin-top: 15px;
  }
}

/deep/ .approve-end {
  position: relative;

  .el-radio-group {
    width: 160px;
  }

  .el-radio {
    margin-bottom: 5px;
    width: 100%;
  }

  .approve-end-leave {
    position: absolute;
    bottom: -5px;
    left: 150px;
  }
}

/deep/ .el-divider--horizontal {
  margin: 10px 0;
}
</style>
