<template>
  <node :title="config.name" :show-error="showError" :content="content" :error-info="errorInfo"
        @selected="$emit('selected')" @delNode="$emit('delNode')" @insertNode="type => $emit('insertNode', type)"
        placeholder="" header-bgc="#ff943e" header-icon="el-icon-s-check"/>
</template>

<script>
import Node from './Node'

export default {
  name: "ApprovalNode",
  props:{
    config:{
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  components: {Node},
  data() {
    return {
      showError: false,
      errorInfo: '',
    }
  },
  computed:{
    content(){
      const config = this.config.props
      switch (config.assignedType){
        case "ASSIGN_USER":
          if (config.assignedUser.length > 0){
            let texts = []
            config.assignedUser.forEach(org => texts.push(org.name))
            return ''+String(texts).replaceAll(',', '')
          }else {
            return ''
          }
        case "SELF":
          return ''
        case "SELF_SELECT":
          return config.selfSelect.multiple ? '':''
        case "LEADER_TOP":
          return ''
        case "LEADER":
          return config.leader.level > 1 ? ' ' + config.leader.level + ' ' : ''
        case "FORM_USER":
          if (!config.formUser || config.formUser === ''){
            return ''
          }else {
            let text = this.getFormItemById(config.formUser)
            if (text && text.title){
              return `${text.title}`
            }else {
              return ''
            }
          }
        case "ROLE":
          if (config.role.length > 0){
            let texts = []
            config.role.forEach(org => texts.push(org.name))
            return ''+String(texts).replaceAll(',', '')
          }else {
            return ''
          }
          default: return ''
      }
    }
  },
  methods: {
    getFormItemById(id){
      return this.$store.state.design.formItems.find(item => item.id === id)
    },
    //
    validate(err){
      try {
        return this.showError = !this[`validate_${this.config.props.assignedType}`](err)
      } catch (e) {
        return true;
      }
    },
    validate_ASSIGN_USER(err){
      if(this.config.props.assignedUser.length > 0){
        return true;
      }else {
        this.errorInfo = ''
        err.push(`${this.config.name} `)
        return false
      }
    },
    validate_SELF_SELECT(err){
      return true;
    },
    validate_LEADER_TOP(err){
      return true;
    },
    validate_LEADER(err){
      return true;
    },
    validate_ROLE(err){
      if (this.config.props.role.length <= 0){
        this.errorInfo = ''
        err.push(`${this.config.name} `)
        return false
      }
      return true;
    },
    validate_SELF(err){
      return true;
    },
    validate_FORM_USER(err){
     if (this.config.props.formUser === ''){
       this.errorInfo = ''
       err.push(`${this.config.name} `)
       return false
     }
      return true;
    },
    validate_REFUSE(err){
      return true;
    },
  }
}
</script>

