<template>
  <div>
    <el-form-item label="">
      <el-input size="small" v-model="value.placeholder" placeholder=""/>
    </el-form-item>
    <el-form-item label="">
      <tip slot="label" content="0"></tip>
      <el-input-number controls-position="right" :precision="0" :max="100" :min="0" size="small" v-model="value.maxSize"  placeholder=""/>
    </el-form-item>
    <el-form-item label="">
      <el-radio name="layout" :label="true" v-model="value.rowLayout"></el-radio>
      <el-radio name="layout" :label="false" v-model="value.rowLayout"></el-radio>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="value.showSummary"></el-switch>
      <el-select v-if="value.showSummary" style="width: 100%;" size="small" v-model="value.summaryColumns" multiple clearable placeholder="">
        <el-option :label="column.title" :value="column.id" v-for="column in columns" :key="column.id"/>
      </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="value.showBorder"></el-switch>
    </el-form-item>
  </div>
</template>

<script>
export default {
  name: "TableListConfig",
  components: {},
  props:{
    value:{
      type: Object,
      default: ()=>{
        return {}
      }
    }
  },
  computed:{
    columns(){
      return this.value.columns.filter(c => c.valueType === 'Number')
    }
  },
  data() {
    return {}
  },
  methods: {}
}
</script>

